  max-width: fit-content;
  display: table;
}
@keyframes strike {
  0% {
    transform-origin: 0% 50%;
    transform: scaleX(0);
  }
  50% {
    transform-origin: 0% 50%;
    transform: scaleX(1);
  }
  50.01% {
    transform-origin: 100% 50%;
    transform: scaleX(1);
  }
  100% {
    transform-origin: 100% 50%;
    transform: scaleX(0);
  }
}
@media (min-width: 1024px) {
  .anim-underline-link:hover:after {
    content: ' ';
    position: absolute;
