.btn {
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  font-size: 12px;
  font-weight: 300;
  background-repeat: no-repeat;
  padding: 7px 11px;
  line-height: 1.2em;
  border: 1px solid rgba(@colorPrimary3, 0.3);
  border-radius: 50px;
  font-family: @title-font;
  color: @navy;
  transition: @transFast;
  background: transparent;

  &__icon {
    margin-left: 14px;
    transition: @transFast;
    * {
      stroke: #fff;
    }
  }

  &__text {
    transition: @transFast;
  }

  &_big {
    padding: 13px 19px;
  }

  &_glass-like {
    border: 1px solid rgba(#fff, 0.3);
    background: rgba(#fff, 0.1);
    .btn__text {
      color: #fff;
    }
    &.btn_dark {
      border: 1px solid rgba(#000, 0.3);
      background: rgba(#000, 0.1);
      .btn__text {
        color: #fff;
      }
    }
  }

  &_trans {
    background: transparent;
    border: 1px solid rgba(255, 255, 255, 0.53);
    color: white;
    font-size: 12px;
    font-weight: 300;
  }

  &_frost {
    border-color: transparent;
    background: rgba(#fff, 0.3);
    color: #fff;
    &.btn_dark {
      background: rgba(#000, 0.3);
      color: #fff;
    }
  }

  &_primary-color {
    border-color: transparent;
    background: @colorPrimary;
    color: #fff;
    svg.btn__icon.icon-arrow-svg {
      * {
        stroke: #fff;
      }
    }
  }

  &_with-arrow {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    max-width: fit-content;
    &.btn_dark {
    }
  }

  &_dark {
    color: @colorPrimary3;
  }

  @media (min-width: @hoverW) {
    &_trans {
      &:hover {
        text-shadow: 0px 0px 6px rgba(255, 255, 255, 1);
        box-shadow: 0px 5px 40px -10px rgba(0, 0, 0, 0.57);
      }
    }
    &_frost {
      &:hover {
        background: rgba(#fff, 0.4);
        &.btn_dark {
          background: rgba(#000, 0.4);
        }
      }
    }
    &_with-arrow {
      &:hover {
        .btn__text {
          text-shadow: 0px 0px 6px rgba(255, 255, 255, 1);
        }
        .btn__icon {
          transform: translateX(3px);
        }
      }
    }
  }
}

.site-btn {
  background: rgba(255, 255, 255, 0.39);
  border: none;
  color: white;
  padding: 7px 15px;
}

.subscribe-button {
  background: @cyan;
  border: none;
  color: white;
}

.blue-btn {
  background: @cyan;
  border: none;
  color: white;
  &.dark {
    background: @blue;
  }
  &.like-btn,
  &.dislike-btn {
    font-weight: 600;
    padding: 8px 22px;
    font-size: 13px;
    margin-bottom: 5px;
    &.dark {
      background: @navy;
    }
  }
}

.myth-btn {
  font-size: 13px;
  font-weight: 600;
  padding: 7px 21px;
  background: @navy;
  color: white;
}

& .more-btn {
  background: @blue;
  position: relative;
  padding: 7px 12px;
  padding-right: 47px;
  border: none;
  color: white;
  &:after {
    position: absolute;
    content: url('../img/arr-w.png');
    right: 13px;
    top: 7px;
  }
  &:hover {
    background: @red;
  }
}

&.job-btn {
  padding: 7px 10px;
  padding-right: 42px;
  border: none;
  background: @light-blue;
  font-weight: 600;
  color: white;
  font-size: 13px;
  position: relative;
  top: -6px;
  background-image: url(../img/mai-wl.png);
  background-repeat: no-repeat;
  background-position: 90%;
}

.form__btn {
  font-size: 12px;
  color: white;
  background: @blue;
  border: none;
  position: relative;
  top: -7px;
  padding: 7px 14px;
  float: right;
}

&.disabled {
  background: #f1f3f4;
  color: #a0a7b9;
  border: 1px solid #dce4e8;
}

.app-btn {
  font-size: 14px;
  color: white;
  background: @blue;
  position: relative;
  padding: 12px 17px;
  padding-left: 45px;
  background-repeat: no-repeat;
  border-radius: 5px;
  border: none;
  &.ios-btn {
    background-image: url('../img/ios-icon.png');
    background-position: 13%;
  }
  &.and-btn {
    background-image: url('../img/android-icon.png');
    background-position: 13%;
  }
}

.arr-btn {
  font-size: 16px;
  color: white;
  padding: 12px 20px;
  padding-right: 65px;
  position: relative;
  font-weight: 500;
  &:after {
    position: absolute;
    content: url('../img/arr-w.png');
    right: 20px;
    top: 10px;
  }
}

.btn-modal {
  font-size: 12px;
  color: white;
  background: @blue;
  position: relative;
  font-weight: 300;
  border: none;
}

.buy-btn {
  background: #00bfa5;
  color: #fff !important;
  font-size: 13px !important;
  padding-right: 58px;
}
